"use client"

import * as React from "react"
import { createPortal } from "react-dom"

import { cn } from "@/lib/utils"
import { SearchIcon, STLArrowIcon } from "@/icons"

export interface SearchHistoryItem {
  id: string
  text: string
}

export interface SearchBarProps {
  /** 플레이스홀더 텍스트 */
  placeholder?: string
  /** 검색어 */
  value?: string
  /** 검색어 변경 핸들러 */
  onChange?: (value: string) => void
  /** 검색 실행 핸들러 (Enter 키) */
  onSearch?: (value: string) => void
  /** 최근 검색어 목록 */
  recentSearches?: SearchHistoryItem[]
  /** 최근 검색어 클릭 핸들러 */
  onRecentSearchClick?: (item: SearchHistoryItem) => void
  /** 추가 className */
  className?: string
  /** 비활성화 */
  disabled?: boolean
}

const SearchBar = React.forwardRef<HTMLInputElement, SearchBarProps>(
  (
    {
      placeholder = "검색어를 입력하세요",
      value,
      onChange,
      onSearch,
      recentSearches = [],
      onRecentSearchClick,
      className,
      disabled,
    },
    ref
  ) => {
    const [open, setOpen] = React.useState(false)
    const [internalValue, setInternalValue] = React.useState("")
    const [highlightedIndex, setHighlightedIndex] = React.useState(-1)
    const [dropdownPosition, setDropdownPosition] = React.useState({ top: 0, left: 0, width: 0 })
    const containerRef = React.useRef<HTMLDivElement>(null)
    const inputWrapperRef = React.useRef<HTMLDivElement>(null)
    const dropdownRef = React.useRef<HTMLDivElement>(null)

    const searchValue = value !== undefined ? value : internalValue

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      const newValue = e.target.value
      if (value === undefined) {
        setInternalValue(newValue)
      }
      onChange?.(newValue)
      setHighlightedIndex(-1)
    }

    const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
      const itemCount = recentSearches.length

      if (e.key === "ArrowDown") {
        e.preventDefault()
        if (!open && itemCount > 0) {
          setOpen(true)
          setHighlightedIndex(0)
        } else if (open && itemCount > 0) {
          setHighlightedIndex((prev) => (prev + 1) % itemCount)
        }
      }

      if (e.key === "ArrowUp") {
        e.preventDefault()
        if (open && itemCount > 0) {
          setHighlightedIndex((prev) => (prev - 1 + itemCount) % itemCount)
        }
      }

      if (e.key === "Enter") {
        if (open && highlightedIndex >= 0 && recentSearches[highlightedIndex]) {
          handleRecentClick(recentSearches[highlightedIndex])
        } else {
          onSearch?.(searchValue)
          setOpen(false)
        }
      }

      if (e.key === "Escape") {
        setOpen(false)
        setHighlightedIndex(-1)
      }
    }

    const handleRecentClick = (item: SearchHistoryItem) => {
      if (value === undefined) {
        setInternalValue(item.text)
      }
      onChange?.(item.text)
      onRecentSearchClick?.(item)
      setOpen(false)
      setHighlightedIndex(-1)
    }

    // 드롭다운 위치 계산
    React.useEffect(() => {
      if (open && inputWrapperRef.current) {
        const rect = inputWrapperRef.current.getBoundingClientRect()
        setDropdownPosition({
          top: rect.bottom + 13,
          left: rect.left,
          width: rect.width,
        })
      }
    }, [open])

    // 외부 클릭 감지
    React.useEffect(() => {
      const handleClickOutside = (e: MouseEvent) => {
        const target = e.target as Node
        const isInsideContainer = containerRef.current?.contains(target)
        const isInsideDropdown = dropdownRef.current?.contains(target)

        if (!isInsideContainer && !isInsideDropdown) {
          setOpen(false)
          setHighlightedIndex(-1)
        }
      }

      if (open) {
        document.addEventListener("mousedown", handleClickOutside)
      }

      return () => {
        document.removeEventListener("mousedown", handleClickOutside)
      }
    }, [open])

    const showDropdown = open && recentSearches.length > 0

    return (
      <>
        <div ref={containerRef} className="relative">
          <div
            ref={inputWrapperRef}
            className={cn(
              "relative flex h-9 items-center gap-2 rounded-[20px] border",
              "bg-white dark:bg-slate-800",
              "border-slate-100 dark:border-slate-600",
              "px-3 cursor-text",
              "focus-within:border-slate-500 dark:focus-within:border-slate-100",
              "transition-colors",
              disabled && "opacity-50 cursor-not-allowed",
              className
            )}
          >
            <SearchIcon size={20} className="text-slate-500 dark:text-slate-50 shrink-0" />
            <input
              ref={ref}
              type="text"
              value={searchValue}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              onFocus={() => !disabled && setOpen(true)}
              placeholder={placeholder}
              disabled={disabled}
              className={cn(
                "flex-1 bg-transparent text-xs outline-none",
                "text-slate-900 dark:text-slate-50",
                "placeholder:text-slate-300 dark:placeholder:text-slate-50",
                "disabled:cursor-not-allowed"
              )}
            />
            <STLArrowIcon size={24} className="text-blue-500 dark:text-slate-50 shrink-0" />
          </div>
        </div>

        {showDropdown && createPortal(
          <div
            ref={dropdownRef}
            style={{
              position: "fixed",
              top: dropdownPosition.top,
              left: dropdownPosition.left,
              width: dropdownPosition.width,
            }}
            className={cn(
              "z-50",
              "overflow-hidden rounded-[5px] border",
              "border-slate-100 dark:border-slate-600",
              "bg-white/90 dark:bg-slate-800/90 backdrop-blur-[12px]",
              "shadow-[10px_10px_10px_0px_rgba(0,0,0,0.05)]",
              "p-[5px]",
              "animate-in fade-in-0 zoom-in-95 slide-in-from-top-2"
            )}
          >
            {recentSearches.map((item, index) => (
              <button
                key={item.id}
                type="button"
                onClick={() => handleRecentClick(item)}
                onMouseEnter={() => setHighlightedIndex(index)}
                className={cn(
                  "flex h-[29px] w-full cursor-pointer select-none items-center rounded-[2px] px-[5px] py-[5px]",
                  "text-xs text-slate-500 dark:text-slate-300 text-left",
                  "hover:bg-slate-100 dark:hover:bg-slate-700",
                  "focus:bg-slate-100 dark:focus:bg-slate-700 outline-none",
                  "transition-colors",
                  highlightedIndex === index && "bg-slate-100 dark:bg-slate-700"
                )}
              >
                {item.text}
              </button>
            ))}
          </div>,
          document.body
        )}
      </>
    )
  }
)
SearchBar.displayName = "SearchBar"

export { SearchBar }
